body {
    margin: 0;
    padding: 0;
    min-width: 100%;
}

table {
    border-spacing: 0;
    font-family: 'AvenirNext-Regular', Helvetica, Arial, sans-serif;
    font-size: 18px;
    line-height: 26px;
    color: #333333;
}

td {
    padding: 0;
}

img {
    border: 0;
}

.test { color: #fff; }

.wrapper {
    width: 100%;
    table-layout: fixed;
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
    background-color: #ededed;
}

.webkit {
    max-width: 600px;
    padding-top: 15px;
    padding-bottom: 15px;
}

.outer {
    margin: 0 auto;
    width: 100%;
    max-width: 600px;
}

.inner {
    padding: 0 15px;
    margin: 0 auto;
    width: 100%;
    max-width: 600px;
}

.full-width-image img {
    width: 100%;
    max-width: 600px;
    height: auto;
}

p {
    margin: 0;
}

.center {
    text-align: center;
}

a {
    color: #ee6a56;
    text-decoration: underline;
}

.h1 {
    font-size: 36px;
    line-height: 36px;
    font-weight: 400;
    margin: 0;
    padding: 40px 40px 0;
    text-transform: uppercase;
}

.h2 {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 12px;
}

.contents {
    width: 100%;
}

.copy {
    padding: 30px 40px 0;
}

.cta {
    padding: 35px 40px 40px;
}

.cta-button {
    display: inline-block;
    padding: 10px 30px;
    background-color: #eb586f;
    color: #ffffff;
    font-size: 16px;
    line-height: 24px;
    text-decoration: none;
    text-transform: uppercase;
    letter-spacing: 1px;
    border: 2px solid #eb586f;
    &.ghost {
        background-color: transparent;
        border-color: #ffffff;
        &.dark {
            border-color: #000000;
            color: #000000;
        }
    }
}

.spacer {
    height: 30px;
}

.header-v1 {
    background-color: #ffffff;
}

.header-v2 {
    background-color: #333333;
    color: #ffffff;
}

.header-v3 {
    background-color: #ffffff;
}

.columns {
    text-align: center;
    background-color: #ffffff;
    font-size: 0;
    
    .image-head {
        padding-top: 15px;
    }
    
    img {
        width: 100%;
        max-width: 280px;
        height: auto;
    }
    
    .text {
        padding-top: 10px;
        padding-bottom: 15px;
        font-size: 16px;
        line-height: 22px;
    }
    
    &.two-column {
        .column {
            width: 100%;
            max-width: 285px;
            display: inline-block;
            vertical-align: top;
            @media screen and (max-width: 400px) {
                max-width: 100% !important;
                img {
                    max-width: 100% !important;
                }
            }
            @media screen and (min-width: 401px) {
                max-width: 50% !important;
            }
        }        
    }

    &.three-column {
        .column {
            width: 100%;
            max-width: 185px;
            display: inline-block;
            vertical-align: top;
            @media screen and (max-width: 400px) {
                max-width: 100% !important;
                img {
                    max-width: 100% !important;
                }
            }
            @media screen and (min-width: 401px) {
                max-width: 33% !important;
            }
        }
    }
}
